body {
  color: #e7e7e7;
  background-color: #000;
  height: 100vh;
  font-family: Trebuchet MS, Lucida Sans Unicode;
}

.header {
  text-align: center;
}

.logo {
  filter: grayscale();
  cursor: pointer;
  width: 380px;
  height: 200px;
  margin-top: 2rem;
}

.logo:hover {
  filter: grayscale(0);
}

.body {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  display: flex;
}

.button {
  color: #e7e7e7;
  margin-top: 1rem;
  margin: 45rem, 3rem;
  text-align: center;
  cursor: pointer;
  background-color: #0000;
  border: 1px solid #e7e7e7;
  border-radius: 30px;
  padding: 10px 22px;
  font-size: 1.25rem;
  text-decoration: none;
  display: inline-block;
  box-shadow: 0 8px 16px #0003, 0 6px 20px #00000030;
}

.button:hover {
  color: #131313;
  background-color: #e7e7e7;
}

#champions {
  grid-gap: 10px;
  grid-template-columns: repeat(1, 10fr);
  justify-items: center;
  width: 100%;
  display: grid;
}

.card {
  text-align: center;
  background: none;
  border-radius: 5px;
  margin: 10px;
  padding-bottom: 1.5rem;
  font-family: Trebuchet MS, Lucida Sans Unicode;
  font-size: medium;
  transition: all .8s;
}

.title {
  color: #c99858;
  font-family: serif;
  font-size: .8rem;
  font-weight: bolder;
}

.version {
  background: linear-gradient(#5994a6, #304162);
  border-radius: 30px;
  align-items: center;
  width: 30%;
  margin: auto;
  font-size: .7rem;
  font-weight: 900;
}

.tags {
  text-align: center;
  background: linear-gradient(#5994a6, #304162);
  border-radius: 30px;
  width: 70%;
  margin: 10px auto;
  padding: 10px;
  font-weight: 900;
}

.stats {
  margin-top: 10px;
  padding: 10px;
}

table {
  border-collapse: collapse;
  align-items: center;
  width: 50%;
  margin: auto;
  font-weight: 900;
}

td {
  text-align: center;
}

th {
  background: linear-gradient(#858585, #414e66);
  padding: 8px;
}

td {
  background: linear-gradient(#5994a6, #304162);
  padding: 10px;
}

td:hover {
  color: #000;
  background: linear-gradient(#aaa, #414e66);
}

.partype {
  color: #fff;
  background: linear-gradient(#858585, #414e66);
  border-radius: 30px;
  align-items: center;
  width: 70%;
  margin: auto auto 5px;
  font-size: .9rem;
  font-weight: 900;
}

td th {
  font-size: 1.1rem;
  font-weight: bold;
}

.card img, .full-card img {
  filter: grayscale();
  width: 8.5rem;
  transition: filter .8s;
}

.card img {
  border-radius: 100%;
  width: 5rem;
  height: auto;
  margin-top: 1rem;
}

.card .info {
  display: none;
}

.card:hover {
  border: 1px solid #fff;
  animation: 1s cardInfo;
}

@keyframes cardInfo {
  from {
    height: 0%;
  }

  to {
    height: 89%;
  }
}

@keyframes displayInfo {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.card:hover .info, .full-card:hover .info {
  animation: 2.5s displayInfo;
  display: block;
}

.card:hover, .description:hover, .full-card:hover {
  transition: all .8s;
}

.card:hover img, .full-card:hover img {
  filter: grayscale(0);
}

.full-card img {
  cursor: pointer;
  border-radius: 10px;
  width: 18rem;
  height: auto;
  margin-top: 1rem;
}

.full-card {
  text-align: center;
  background: linear-gradient(#9f9f9f, #22252b);
  border: 1px solid #565656;
  border-radius: 10px;
  width: 20rem;
  height: auto;
  margin: 10px;
  font-family: Trebuchet MS, Lucida Sans Unicode;
  transition: all .8s;
  box-shadow: 0 4px 8px #0003;
}

.full-card:hover .full-img {
  transform: scale(1.01);
}

.description {
  margin: 1rem;
  font-size: .8rem;
}

@media screen and (width >= 700px) and (width <= 1200px) {
  #champions {
    grid-gap: 10px;
    grid-template-columns: repeat(2, 10fr);
    justify-items: center;
    width: 100%;
    display: grid;
  }
}

@media screen and (width >= 1200px) {
  #champions {
    grid-gap: 10px;
    grid-template-columns: repeat(5, 10fr);
    justify-items: center;
    width: 100%;
    display: grid;
  }
}
